@import '../../styles/media.scss';
@import '../../styles/color.scss';
.toolbars {
    display: flex;
    align-items: center;
    position: absolute;
    z-index: 10;
    top: 20px;
    left: 45%;
    .toolbar {
        display: flex;
        align-items: center;
        background: #fff;
        box-shadow: $boxShadow;
        border-radius: 10px;
        .tool {
            display: flex;
            align-items: center;
            position: relative;
            cursor: pointer;
            padding: 10px;
            border-radius: 10px;
            transition: 0.2s;
            margin: 0 2px;
            &:hover svg {
                filter: invert(73%) sepia(18%) saturate(503%) hue-rotate(98deg) brightness(90%) contrast(84%);
            }
            svg {
                width: 15px;
                height: 15px;
            }
            &.active {
                background-color: $primary;
                svg {
                    filter: invert(100%) sepia(4%) saturate(2%) hue-rotate(229deg) brightness(116%) contrast(100%);
                }
                .keycode {
                    color: #fff;
                }
            }
        }
    }
    .fontTool {
        display: flex;
        position: relative;
        align-items: center;
        cursor: pointer;
        padding: 10px;
        border-radius: 10px;
        background-color: #fff;
        transition: 0.2s;
        margin: 0 10px;
        box-shadow: $boxShadow;
        border-radius: 10px;
        input {
            padding: 0 10px;
            max-width: 100px;
        }
        .MuiFormControl-root {
            margin-left: 10px;
            height: 15px;
        }
        .MuiInputBase-input {
            height: 18px;
        }

        .create {
            margin-left: 10px;
            font-weight: 600;
            font-size: 14px;
            &:hover {
                color: $primary;
            }
        }
        svg {
            width: 15px;
            height: 15px;
            &:hover {
                filter: invert(73%) sepia(18%) saturate(503%) hue-rotate(98deg) brightness(90%) contrast(84%);
                
            }
        }
    }
}

.keycode {
    position: absolute;
    font-size: 12px;
    right:2px;
    font-weight: 600;
    bottom: 3px;
    transform: scale(0.9);
}
