@import '../../styles/color.scss';

#container .react-flow__node-custom .react-flow__handle-right {
    transform: none;
}

#container.wrapper {
    position: relative;
    height: 100%;
}

#container .download-btn {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 20px;
    right: 65px;
    z-index: 10;
    border: none;
    background: #fff;
    padding: 10px;
    box-shadow: $boxShadow;
    border-radius: 10px;
    cursor: pointer;
    svg{
        width: 15px;
        height: 15px;
    }
    &:hover {
        svg {
            filter: invert(73%) sepia(18%) saturate(503%) hue-rotate(98deg) brightness(90%) contrast(84%);
        }
    }
}

#container .download-btn:hover {
    opacity: 0.9;
}
