@import '../../styles/media.scss';
@import '../../styles/color.scss';
.node {
    background-color: #fff;
    box-shadow: $boxShadow;
    border-radius: 100%;
    height: 100%;
    width: 100%;
    .main {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        width: 100%;
        .MuiFormControl-root{
            height: 100%;
            width: 60%;
        }
        .MuiInputBase-root {
            height: 100%;
            width: 100%;
            padding: 5px;
        }
        fieldset {
            border: 0 !important;
            outline: none !important;
        }
    }
    &.square {
        border-radius: 0;
    }
    &.rhombus {
        border-radius: 0;
        transform: rotate(45deg);
        overflow: hidden;
        min-width: 100px;
        min-height: 200px;
        display: flex;
        align-items: center;
        justify-content: center;
        textarea {
            transform: rotate(-45deg);
        }
        .react-flow__handle.source{
            left: 10px;
            top: 10px;
        }
    }
    .add-bottom {
        cursor: pointer;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        z-index: 100000;
        color: $black;
    }
}
