@import '../../styles/media.scss';
@import '../../styles/color.scss';

.dashboard-wrapper {
    width: 100%;
    height: calc(100% - 70px);
}

.btn-add {
    position: absolute;
    z-index: 10;
    top: 10px;
    left: 50%;
}
.logo {
    position: absolute;
    z-index: 10;
    top: 20px;
    left: 20px;
}
.react-flow__handle {
    width: 7px;
    height: 7px;
    border-radius: 50px;
    box-shadow: $boxShadow;
}

.touchdevice-flow .react-flow__handle.connecting {
    animation: bounce 1600ms infinite ease-out;
}

.save__controls .restore {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 20px;
    right: 110px;
    z-index: 10;
    border: none;
    background: #fff;
    padding: 10px;
    cursor: pointer;
    box-shadow: $boxShadow;
    border-radius: 10px;
    svg {
        width: 15px;
        height: 15px;
    }
    &:hover {
        svg {
            filter: invert(73%) sepia(18%) saturate(503%) hue-rotate(98deg) brightness(90%) contrast(84%);
        }
    }
}

.save__controls .quest {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    bottom: 140px;
    left: 18px;
    z-index: 10;
    border: none;
    background: $black;
    padding: 6px;
    cursor: pointer;
    box-shadow: $boxShadow;
    border-radius: 50%;
    svg {
        width: 10px;
        height: 10px;
    }
}

.save__controls .save {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 20px;
    right: 155px;
    z-index: 10;
    border: none;
    background: #fff;
    padding: 10px;
    cursor: pointer;
    box-shadow: $boxShadow;
    border-radius: 10px;
    svg {
