    }
    &:hover {
        svg {
            filter: invert(73%) sepia(18%) saturate(503%) hue-rotate(98deg) brightness(90%) contrast(84%);
        }
    }
}

.save__controls .delete {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 20px;
    right: 200px;
    z-index: 10;
    border: none;
    background: #fff;
    padding: 10px;
