@import '../../styles/color.scss';

.questTop {
    display: flex;
    flex-direction: column;
    align-items: center;
    .welcome {
        display: flex;
        align-items: center;
        margin-bottom: 10px;
        svg {
            width: 70px;
            margin-left: 10px;
        }
    }
    img {
        height: 120px;
        width: 270px;
    }
}
.icons {
    display: flex;
    align-items: center;
    margin: 10px 0;
    .icon {
        width: 50%;
        display: flex;
        align-items: center;
        svg {
            margin-right: 10px;
            border-radius: 5px;
            padding: 2px 5px;
            box-shadow: $boxShadow;
            width: 23px;
            height: 20px;
        }
        div {
            font-size: 12px;
        }
    }
}
.questTitle {
    font-size: 14px;
    margin-top: 10px;
}
