@import '../../styles/media.scss';
@import '../../styles/color.scss';

.edgebutton {
    background: #eee;
    border: 1px solid #fff;
    cursor: pointer;
    border-radius: 50%;
    font-size: 12px;
    line-height: 1;
    display: none;
    transition: 0.3s;
}
.edgebutton:hover {
    box-shadow: 0 0 6px 2px rgba(0, 0, 0, 0.08);
}
.edgebutton-foreignobject  {
    transform: translateX(50px);
    height: 100px;
    width: 100px;
    background: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
}